@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;400;700;900&display=swap);
h1,h2{text-align:center}h1{margin:40px 0 30px}h2{text-align:center;margin-bottom:1.2rem}

:root{font-size:16px;--dark: #030912;--white: #FFFDED;--blue: #5688C7;--green: #7ec952;--red: #960200}*{margin:0;padding:0;box-sizing:border-box}body{font-family:Poppins, sans-serif;background-color:#030912;background-color:var(--dark);color:#FFFDED;color:var(--white);margin-bottom:60px}h1{font-weight:900;font-size:3rem}h2{margin-bottom:1rem;font-weight:700}h3{margin-bottom:2rem;font-weight:700}p{margin-bottom:1rem}a{color:#5688C7;color:var(--blue);text-decoration:none}a:hover{color:#5688C7;color:var(--blue)}hr{border:none;height:1px;background-color:#666;margin-bottom:30px}ol,ul{margin:0 0 2rem 2rem}.container{width:90%;margin:auto}.btn{border:0;padding:10px 15px;display:inline-block;cursor:pointer;color:#FFFDED;color:var(--white);background-color:#5688C7;background-color:var(--blue);border-radius:10px;font-size:1.1rem;transition:background-color 300ms ease-in-out;outline:none}.btn:hover{background-color:#7ec952;background-color:var(--green)}.form-control{display:block;width:100%;margin-bottom:20px;padding:10px;border-radius:5px;border:0;font-size:1.1rem;color:#333}.form-control:focus{outline:none}.mb-2{margin-bottom:20px}.mb-4{margin-bottom:40px}.mb-6{margin-bottom:60px}.text-left{text-align:left}.text-center{text-align:center}.text-right{text-align:right}.white{color:#FFFDED;color:var(--white)}.dark{color:#030912;color:var(--dark)}.blue{color:#5688C7;color:var(--blue)}.green{color:#7ec952;color:var(--green)}.red{color:#960200;color:var(--red)}.card-grid{display:grid;grid-template-columns:repeat(auto-fit, minmax(200px, 400px));justify-content:center;grid-gap:40px 25px}.card{max-width:450px;align-content:center;border:2px solid white;overflow:hidden;border-radius:10px}.card p{text-align:center}.card img{width:100%;max-width:450px;height:auto}

